body {
  height: 100vh;
  margin: 0;
  display: flex;
}

.calendar {
  flex-wrap: wrap;
  gap: 1px;
  max-width: 706px;
  margin: auto;
  padding: 10px;
  display: flex;
}

.calendar__day {
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  font-size: 30px;
  transition: all .5s;
  display: flex;
}

.calendar--start-day-mon .calendar__day:first-child {
  margin-left: 0;
}

.calendar--start-day-tue .calendar__day:first-child {
  margin-left: 101px;
}

.calendar--start-day-wed .calendar__day:first-child {
  margin-left: 202px;
}

.calendar--start-day-thu .calendar__day:first-child {
  margin-left: 303px;
}

.calendar--start-day-fri .calendar__day:first-child {
  margin-left: 404px;
}

.calendar--start-day-sat .calendar__day:first-child {
  margin-left: 505px;
}

.calendar--start-day-sun .calendar__day:first-child {
  margin-left: 606px;
}

.calendar--month-length-1 .calendar__day:nth-child(-n+1) {
  box-sizing: border-box;
  background-color: #eee;
  border: 1px solid #000;
  width: 100px;
  height: 100px;
}

.calendar--month-length-1 .calendar__day:nth-child(-n+1):first-child:before {
  content: "1";
}

.calendar--month-length-1 .calendar__day:nth-child(-n+1):hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

